import Author from "../../Elements/Author/Author";
import Network from "../../Elements/Network/Network";
import { isTagEmbed } from "../../../../actions/api";
import Time from "../../Elements/Time/Time";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorNameStyle = {
    color: ThemeRule.authorColor, fontFamily: ThemeRule.css_font
  };
  const hashtagStyle = {
    color: ThemeRule.authorColor,
    fontSize:  ThemeRule.fontSize,
    fontFamily: ThemeRule.css_font,
  };
  const sepratorStyle = {
    backgroundColor: ThemeRule.authorColor
  };
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime) ? true : false;

  return (
    <div className="tb_stt_content_wrap">
      {!isTagEmbed ? <div className="tb_stt_author_img">
        {postAuthor ? <Author author={author} network={network} authorClass={'tb_stt_author_img_00'} /> : ' '}
      </div> : null}
      <div className="tb_stt_author_info">
        <Network networkClass={'tb_stt_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
        <div className="tb_stt_author_001">
          {ThemeRule.hideContent === 0 ? <div className={`tb_stt_hashtag tb-cTBfont-${ThemeRule.font_varient}`} style={!isTagEmbed ? hashtagStyle : null}>{username}</div> : ''}
          {postAuthor ? <div className="tb_stt_author_name" style={!isTagEmbed ? authorNameStyle : null}>{author.name}</div> : ''}
          {/* {authorSeprator ? <div className="tb_stt_seprator" style={!isTagEmbed ? sepratorStyle: null}> </div> : ''} */}
          {/* {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_stt_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} authorNameStyle={authorNameStyle} /> : ''} */}
        </div>
      </div>
    </div>
  );
}
export default AuthorDetails