import React, { PureComponent } from "react";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { connect } from 'react-redux';
import Card from "./Elements/Card";
import { isTagEmbed } from "../../../actions/api";
import '../scss/ThemesSplide.scss'
import './scss/StoryTheme.scss'
class StoryTheme extends PureComponent {
  constructor(props) {
    super(props)
    this.secondaryRef = React.createRef();
  }
  state = {
    arrowWidth: 0,
    currentIndex: 0,
  }
  componentDidMount() {
    this.onResponsiveCardItem()
    window.addEventListener("resize", () => this.onResponsiveCardItem());
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData, completeDataObject, onClickPopUpSlider, wall } = this.props;
    if (this.secondaryRef.current && this.secondaryRef.current.slides.length > 0) {
      this.secondaryRef.current.slides.map((item, itemIndex) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)

          let clickD = document.querySelector(`.tb_stt_post_container #${item.id}`)
          clickD.addEventListener('click', event => {
            event.preventDefault();
            const itemData = completeDataObject[getID]
            if (wall.Personalization.widgetTheme == 56 || !isTagEmbed) {
              onClickPopUpSlider(findIndex, itemData)
            }
          });
        }
      })
    }
  }
  onResponsiveCardItem = () => {
    const itemSize = document.querySelector(".splide__slide.is-active")
    if (itemSize) {
      const arrowWidth = itemSize.clientWidth + itemSize.clientWidth * 0.10;
      this.setState({ arrowWidth })
    }
  }
  handleVideoEnded = () => {
    const { postData } = this.props;
    const { currentIndex } = this.state
    const nextSlideIndex = currentIndex + 1;
    if (nextSlideIndex < postData.length) {
      if (this.secondaryRef.current && this.secondaryRef.current) {
        this.secondaryRef.current.splide.go(nextSlideIndex)
      }
    }
  };

  render() {
    const { completeDataObject, wall, onClickToCTA, modalPop, onClickPopUpSlider } = this.props;
    const { arrowWidth, currentIndex } = this.state;
    let postData = this.props.postData;
    if (postData && postData.length > 0) {
      postData = postData.length == 1 ? [...postData, ...postData, ...postData, ...postData] : postData.length == 2 ? [...postData, ...postData] : this.props.postData
    }

    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const cloneStatus = isTagEmbed && wall.Personalization?.clonePost !== undefined ? wall.Personalization.clonePost === 0 ? { clones: 0 } : {} : {};
    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length > 0 ? false : 'loop' : 'loop'
    const settings = {
      ...cloneStatus,
      type: isType,
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : 7,
      perMove: 1,
      gap: 0,
      cloneStatus: false,
      autoplay: slidePost === 1 ? true : false,
      padding: "0",
      pagination: false,
      arrows: true,
      focus: 'center',
      start: wall.Wall.owner == 127183 ? 0 : 2,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
          start: wall.Wall.owner == 127183 ? 0 : 2,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
          start: wall.Wall.owner == 127183 ? 0 : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
          start: 2,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
          start: wall.Wall.owner == 127183 ? 0 : 2,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
          start: wall.Wall.owner == 127183 ? 0 : 2,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
          start: wall.Wall.owner == 127183 ? 0 : 2,
        }
      },
      updateOnMove: true,
    };
    const mobileClass560 = settings.breakpoints[560].perPage === 1 ? 'tb_stt_center_mode560' : '';
    const mobileClass767 = settings.breakpoints[767].perPage === 1 ? 'tb_stt_center_mode767' : '';
    const mobileClass991 = settings.breakpoints[991].perPage === 1 ? 'tb_stt_center_mode991' : '';
    if (isTagEmbed && slidePost === 1) {
      var Autoplay = this.secondaryRef && this.secondaryRef.current ? this.secondaryRef.current.splide.Components.Autoplay : null;
      if (modalPop.isShowPopUp && Autoplay) Autoplay.pause()
      else {
        if (Autoplay) Autoplay.play()
      }
    }
    return (
      <div className="tb_stt_post_container">
        <Splide hasTrack={false} className={`tb_stt_post_slider ${isTagEmbed ? 'tb_stt_triple_slider' : ''} ${mobileClass560} ${mobileClass767} ${mobileClass991}`} options={settings} ref={this.secondaryRef} onActive={(splide, prev, next) => this.setState({ currentIndex: prev.index })}>
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} style={{ margin: 0, padding: 0 }} data-index={index} className={`tb_${cardData.id}`}>
                  <Card ownerId={wall.Wall.owner} itemData={cardData} autoPlay={currentIndex == index} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} wall={wall} handleVideoEnded={this.handleVideoEnded} onClickPopUpSlider={onClickPopUpSlider} />
                </SplideSlide>
              })}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_stt_arrow  tb_stt_arrow_left__" onClick={this.onUpdateData} style={{ right: arrowWidth }} role="button" aria-label="Go to previous slide">
            <div className="splide__arrow splide__arrow--prev tb__icon tb-arrow-left-alt"> </div>
          </div>
          <div className="splide__arrows splide__arrows--ltr tb_stt_arrow tb_stt_arrow_right__" style={{ left: arrowWidth }} role="button" aria-label="Go to next slide">
            <div className="splide__arrow splide__arrow--next tb__icon tb-arrow-right-alt"> </div>
          </div>
        </Splide>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    modalPop: state.modalPop
  }
}
export default connect(mapStateToProps)(StoryTheme);
