import Image from '../../Elements/Media/Image'
import Video from "../../Elements/Media/Video";
import { isTagEmbed } from '../../../../actions/api';
const Media = ({ itemData, wallID, ownerId, wall, autoVPlay, handleVideoEnded }) => {
  const autoPlay = ((itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && (wall.ThemeRule.autoPlay == 1)) && autoVPlay ? true : false
  return (<div className="tb_stt_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">
    {autoPlay ?
      <Video VideoClass="tb_stt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} controls={false} autoPlay={isTagEmbed && autoVPlay ? true : autoPlay ? autoVPlay : false} handleVideoEnded={handleVideoEnded} muted={true} isCover={true} />
      :
      <Image ImageClass="tb_stt_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} />
    }
  </div>)
}
export default Media