import { useEffect } from "react";
import Media from "./Media";
import AuthorDetails from "./AuthorDetails";
import CTAButton from "../../Elements/CTA/CtaButton";
import { isTagEmbed } from "../../../../actions/api";
const Card = ({ itemData, personalization, adjustWidth, ThemeRule, wallID, ownerId, wall, onClickToCTA, autoPlay, handleVideoEnded, onClickPopUpSlider, itemIndex }) => {
  const cardSize = {
    width: `${adjustWidth}%`
  };
  const mediaType = itemData.type !== 1 ? true : false;
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return (
    <div id={`tb-stt-post-${itemData.id}`} className={`tb_stt_post_wrapper`} style={cardSize} tb-network={itemData.network.icon}>
      <div className="tb_stt_card_wrapper">
        {mediaType ?
          <div className="tb_stt_media_img">
            <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} autoVPlay={autoPlay} handleVideoEnded={handleVideoEnded} />
          </div>
          : ''}
        <div className="tb_stt_card_wrapper_00">
          <div className="tb_stt_card_content_0">
            {ctaStatus && isTagEmbed ? <div className="tb_stt_post_cta"><CTAButton ctaClass={'tb_stt_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
            <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
          </div>
        </div>
      </div>
    </div>
  );
}
export default Card